/**
 * SupabaseË®≠ÂÆöÊ§úË®º„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£
 *
 * „Åì„ÅÆ„É¢„Ç∏„É•„Éº„É´„ÅØSupabase„ÅÆË®≠ÂÆö„ÅåÊ≠£„Åó„ÅèË°å„Çè„Çå„Å¶„ÅÑ„Çã„Åã„ÇíÊ§úË®º„Åó„Åæ„Åô„ÄÇ
 */

import { SUPABASE_CONFIG } from './constants.js';

/**
 * SupabaseË®≠ÂÆö„ÅÆÊ§úË®ºÁµêÊûú
 */
export class SupabaseConfigValidation {
  constructor() {
    this.isValid = false;
    this.errors = [];
    this.warnings = [];
    this.suggestions = [];
  }

  addError(message) {
    this.errors.push(message);
    this.isValid = false;
  }

  addWarning(message) {
    this.warnings.push(message);
  }

  addSuggestion(message) {
    this.suggestions.push(message);
  }
}

/**
 * SupabaseË®≠ÂÆö„ÇíÊ§úË®º
 * @returns {SupabaseConfigValidation} Ê§úË®ºÁµêÊûú
 */
export function validateSupabaseConfig() {
  const validation = new SupabaseConfigValidation();

  // URL„ÅÆÊ§úË®º
  if (!SUPABASE_CONFIG.url) {
    validation.addError('Supabase URL„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì');
  } else if (SUPABASE_CONFIG.url.includes('your-project-id')) {
    validation.addError('Supabase URL„Åå„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„Éº„ÅÆ„Åæ„Åæ„Åß„Åô');
    validation.addSuggestion(
      'js/utils/constants.js„ÅßÂÆüÈöõ„ÅÆ„Éó„É≠„Ç∏„Çß„ÇØ„ÉàURL„ÇíË®≠ÂÆö„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
    );
  } else if (!SUPABASE_CONFIG.url.startsWith('https://')) {
    validation.addError('Supabase URL„ÅØhttps://„ÅßÂßã„Åæ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô');
  } else if (!SUPABASE_CONFIG.url.endsWith('.supabase.co')) {
    validation.addError('Supabase URL„ÅØ.supabase.co„ÅßÁµÇ„Çè„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„Åô');
  } else {
    // URL„ÅÆÂΩ¢Âºè„ÅØÊ≠£„Åó„ÅÑ
    validation.isValid = true;
  }

  // API Key„ÅÆÊ§úË®º
  if (!SUPABASE_CONFIG.key) {
    validation.addError('Supabase API Key„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì');
    validation.isValid = false;
  } else if (SUPABASE_CONFIG.key.includes('your-anon-key')) {
    validation.addError('Supabase API Key„Åå„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„Éº„ÅÆ„Åæ„Åæ„Åß„Åô');
    validation.addSuggestion(
      'js/utils/constants.js„ÅßÂÆüÈöõ„ÅÆAPI Key„ÇíË®≠ÂÆö„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
    );
    validation.isValid = false;
  } else if (!SUPABASE_CONFIG.key.startsWith('eyJ')) {
    validation.addError(
      'Supabase API Key„ÅØJWT„Éà„Éº„ÇØ„É≥„Åß„ÅÇ„ÇãÂøÖË¶Å„Åå„ÅÇ„Çä„Åæ„ÅôÔºàeyJ„ÅßÂßã„Åæ„ÇãÔºâ'
    );
    validation.isValid = false;
  }

  // Ë®≠ÂÆö„ÅåÊ≠£„Åó„ÅÑÂ†¥Âêà„ÅÆËøΩÂä†„ÉÅ„Çß„ÉÉ„ÇØ
  if (validation.isValid) {
    validation.addSuggestion(
      'Ë®≠ÂÆö„ÅØÊ≠£„Åó„ÅèË¶ã„Åà„Åæ„Åô„ÄÇÊé•Á∂ö„ÉÜ„Çπ„Éà„ÇíÂÆüË°å„Åó„Å¶„Åè„Å†„Åï„ÅÑ'
    );
  }

  return validation;
}

/**
 * Ë®≠ÂÆö„ÅÆÊ§úË®ºÁµêÊûú„Çí„Ç≥„É≥„ÇΩ„Éº„É´„Å´Ë°®Á§∫
 * @param {SupabaseConfigValidation} validation Ê§úË®ºÁµêÊûú
 */
export function displayValidationResults(validation) {
  console.log('\nüîç SupabaseË®≠ÂÆöÊ§úË®ºÁµêÊûú:');

  if (validation.isValid) {
    console.log('‚úÖ Ë®≠ÂÆö„ÅØÊ≠£Â∏∏„Åß„Åô');
  } else {
    console.log('‚ùå Ë®≠ÂÆö„Å´ÂïèÈ°å„Åå„ÅÇ„Çä„Åæ„Åô');
  }

  if (validation.errors.length > 0) {
    console.log('\nüö® „Ç®„É©„Éº:');
    validation.errors.forEach((error) => {
      console.log(`   ‚Ä¢ ${error}`);
    });
  }

  if (validation.warnings.length > 0) {
    console.log('\n‚ö†Ô∏è Ë≠¶Âëä:');
    validation.warnings.forEach((warning) => {
      console.log(`   ‚Ä¢ ${warning}`);
    });
  }

  if (validation.suggestions.length > 0) {
    console.log('\nüí° ÊèêÊ°à:');
    validation.suggestions.forEach((suggestion) => {
      console.log(`   ‚Ä¢ ${suggestion}`);
    });
  }
}

